import React, { Component } from 'react';

import pic from '../img/1.png';
import { Nav } from "react-bootstrap";
import { Link } from "react-router-dom";

class Home extends Component {
    render() {
        return (
            <div className="wrapper">
                <div id="first">
                    <img src = {pic} alt = "Profile Pic" className = "profilepic" />
                </div>
                <div id="second">
                    <h2>Hello,</h2><br/>
                    <h3>a bit about me:</h3>
                    <div className = "buttonContainer">
                        <Nav>
                            <Nav.Link><Link to={"/resume"} className = "btnLink link1">Resume</Link></Nav.Link>
                            <Nav.Link><Link to={"/projects"} className = "btnLink link2">Projects</Link></Nav.Link>
                            <Nav.Link><Link to={"/contact"} className = "btnLink link3">Contact</Link></Nav.Link>
                        </Nav>
                    </div>
                    <div>
                    <p style={{textAlign: "justify"}}>I am Gaurav Salvi, a web designer/developer focused on crafting great web experiences. 
                        Designing and Coding have been my passion since the days I started working with computers but I found myself into web design/development since 2007. 
                        I enjoy creating beautifully designed, intuitive and functional websites.
                        For over past 8 years, I have worked for some of the best digital agencies and wonderful clients to create some awesome websites. 
                        And, I can make this happen for your business as well.</p>
                    </div>                    
                </div>
            </div>
        )
    }
}

export default Home
